import React, { Component } from 'react';
import { connect } from "react-redux";
import SVG from 'react-inlinesvg'
//import Select from 'react-select';
import { Accordion, ToggleButtonGroup, ToggleButton, Button } from 'react-bootstrap';
import { baseUrl } from '../../../../Assets/AssetsHelper';
import Filters from './Filters';
import Templates from './Template';


const selectedPlatform = (platform) => {
    switch (platform) {
        case 'p-html':
            return 'HTML'
        case 'p-wordpress':
            return 'Wordpress'
        case 'p-wix':
            return 'Wix'
        case 'p-squarespace':
            return 'Squarespace'
        case 'p-shopify':
            return 'Shopify'
        case 'p-other':
            return 'Other'
        default:
            return `HTML`
    }
}

class WebEmbedAside extends Component {
    constructor(props) {
        super(props);
    }
    state = {
        showModal: false,
        //setValue : '',
        publishList: [],
    };
    componentDidMount() {
        // this.onLoadPublish()
    }
    // onLoadPublish = () => {
    //     var self = this
    //     getPublishList().then((loadReponse) => {
    //         self.setState({ publishList: loadReponse.data })
    //     })
    // }
    componentDidMount() {
        const clickResetFilter = document.querySelector("#web_tab_panel-tab-aside-assets_filter")
        if (clickResetFilter) {
            clickResetFilter.addEventListener('click', (event) => this.props.onUpdateReponseData(null));
        }
    }
    handleSelect(date) {

    }
    embedCodeCallback = event => this.props.genratedCodeCallback(event)


    render() {
        //const { setValue } = this.state
        const { filterApply, state, genratedCodeModal, onClickToDelete, onResetFilter, activeFilter, history, onChangeName, isPublishEdit, templeteList, themeSkin, isHide, onSelectActive } = this.props
        return <>
            <label className="text-gray-800 mb-1 fw-medium" htmlFor='filterLabel'>Widget Name <strong className="text-danger">*</strong></label>
            <input type="text" className="form-control form-control-sm mb-3" id="filterLabel" placeholder="Please enter publish name.." value={state.publishCodeName} onChange={onChangeName} />
            <div className='filter_wrapper' data-button={isHide ? "0" : "1"}>
                <Accordion defaultActiveKey={1} activeKey={state.activeKey} onSelect={onSelectActive(1) ? onSelectActive(1) : null}>
                    <Accordion.Item eventKey={1} className="pb-3">
                        <Accordion.Header className="add_fillter_btn">
                            <i className="ri-add-line plus_symbol me-1"></i>
                            <i className="ri-subtract-line minus_symbol me-1"></i>
                            <span className="fs-9 fw-medium">Choose Filter</span>
                        </Accordion.Header>
                        <Accordion.Body className="border rounded-1 mt-1 choose_filter">
                            <div className={`d-flex align-items-center border-bottom py-2 cursor-pointer ${Object.keys(state.chooseFilter).includes("collection") ? `d-none disabled` : ``}`} onClick={filterApply("collection")}>
                                <div className="symbol symbol-28 bg-light me-2">
                                    <div className="symbol-label">
                                        <i className="ri-folder-4-line icon-7 text-gray-800"></i>
                                    </div>
                                </div>
                                <div className="d-flex flex-column">
                                    <p className={`fs-9 fw-medium mb-0 text-gray-800`}>Collection</p>
                                    <span className="fs-10 text-gray-500">Select the preferred collection</span>
                                </div>
                            </div>
                            <div className={`d-flex align-items-center border-bottom py-2 cursor-pointer ${Object.keys(state.chooseFilter).includes("source") ? `d-none disabled` : ``}`} onClick={filterApply("source")}>
                                <div className="symbol symbol-28 bg-light me-2">
                                    <div className="symbol-label">
                                        <span className="svg-icon svg-icon-7 text-gray-800 me-0">
                                            <SVG src={baseUrl('media/icons/svg-icons/network.svg')} height={18} width={18} />
                                        </span>
                                    </div>
                                </div>
                                <div className="d-flex flex-column">
                                    <p className={`fs-9 fw-medium mb-0 text-gray-800`}>Network</p>
                                    <span className="fs-10 text-gray-500">Choose the required network</span>
                                </div>
                            </div>
                            <div className={`d-flex align-items-center border-bottom py-2 cursor-pointer  ${Object.keys(state.chooseFilter).includes("connection") ? `d-none disabled` : ``}`} onClick={filterApply("connection")}>
                                <div className="symbol symbol-28 bg-light me-2">
                                    <div className="symbol-label">
                                        <span className="svg-icon svg-icon-7 text-gray-800 me-0">
                                            <SVG src={baseUrl('media/icons/svg-icons/network-manager.svg')} height={18} width={18} />
                                        </span>
                                    </div>
                                </div>
                                <div className="d-flex flex-column">
                                    <p className={`fs-9 fw-medium mb-0 text-gray-800`}>Connections</p>
                                    <span className="fs-10 text-gray-500">Select the connection type</span>
                                </div>
                            </div>
                            <div className={`d-flex align-items-center border-bottom py-2 cursor-pointer  ${Object.keys(state.chooseFilter).includes("assetsType") ? `d-none disabled` : ``}`} onClick={filterApply("assetsType")}>
                                <div className="symbol symbol-28 bg-light me-2">
                                    <div className="symbol-label">
                                        <i className="ri-image-line icon-7 text-gray-800"></i>
                                    </div>
                                </div>
                                <div className="d-flex flex-column">
                                    <p className={`fs-9 fw-medium mb-0 text-gray-800`}>Assets Type</p>
                                    <span className="fs-10 text-gray-500">Choose the type of asset</span>
                                </div>
                            </div>
                            <div className={`d-flex align-items-center border-bottom py-2 cursor-pointer ${Object.keys(state.chooseFilter).includes("tags") ? `d-none disabled` : ``}`} onClick={filterApply("tags")}>
                                <div className="symbol symbol-28 bg-light me-2">
                                    <div className="symbol-label">
                                        <i className="ri-price-tag-3-line icon-7 text-gray-800"></i>
                                    </div>
                                </div>
                                <div className="d-flex flex-column">
                                    <p className={`fs-9 fw-medium mb-0 text-gray-800`}>Tag</p>
                                    <span className="fs-10 text-gray-500">Select the preferred tag</span>
                                </div>
                            </div>
                            <div className={`d-flex align-items-center py-2 cursor-pointer ${Object.keys(state.chooseFilter).includes("ugcrights") ? `d-none disabled` : ``}`} onClick={filterApply("ugcrights")}>
                                <div className="symbol symbol-28 bg-light me-2">
                                    <div className="symbol-label">
                                        <span className="svg-icon svg-icon-7 text-gray-800 me-0">
                                            <SVG src={baseUrl('media/icons/svg-icons/rights-approved-line.svg')} height={18} width={18} />
                                        </span>
                                    </div>
                                </div>
                                <div className="d-flex flex-column">
                                    <p className={`fs-9 fw-medium mb-0 text-gray-800`}>UGC Rights</p>
                                    <span className="fs-10 text-gray-500">Select the preferred ugc rights status</span>
                                </div>
                            </div>
                        </Accordion.Body>
                    </Accordion.Item>
                </Accordion>
                {
                    state.chooseFilter && Object.keys(state.chooseFilter).map((filterKey, indexFilter) => <Filters filterKey={filterKey} {...this.props} indexFilter={indexFilter} isLast={Object.keys(state.chooseFilter)[Object.keys(state.chooseFilter).length - 1]} />)
                }
                <Accordion defaultActiveKey={''}>

                    <Templates {...this.props} key={templeteList.length} />
                    <label className="text-gray-800 mb-2">Select Platform</label>
                    <Accordion.Item eventKey="9" className="pb-3">
                        <Accordion.Header className="w-100">
                            <span className="fs-9 fw-medium">{selectedPlatform(genratedCodeModal)}</span>
                        </Accordion.Header>
                        <Accordion.Body className="border rounded-1 mt-1">
                            <ToggleButtonGroup type="radio" className="flex-column w-100 btn-group-outline" name="platform" defaultValue={'p-html'}>
                                <ToggleButton className={`mb-2 bg-transparent ${genratedCodeModal == "p-html" ? `active` : ``}`} variant="outline-secondary" id="p-html" value={'p-html'} onClick={e => this.props.genratedCodeCallback("p-html")}>
                                    <div className="block-center text-center h-46px w-100">
                                        <img className="img-fluid" src={baseUrl(`media/images/platform/html${themeSkin == 'dark' ? '-dark' : ''}.svg`)} alt="html" height={23} width={90} />
                                    </div>
                                </ToggleButton>
                                <ToggleButton className={`mb-2 bg-transparent ${genratedCodeModal == "p-wordpress" ? `active` : ``}`} variant="outline-secondary" id="p-wordpress" value={'p-wordpress'} onClick={e => this.props.genratedCodeCallback("p-wordpress")}>
                                    <div className="block-center text-center h-46px w-100">
                                        <img className="img-fluid" src={baseUrl(`media/images/platform/wordpress${themeSkin == 'dark' ? '-dark' : ''}.svg`)} alt="wordpress" height={39} width={80} />
                                    </div>
                                </ToggleButton>
                                <ToggleButton className={`mb-2 bg-transparent ${genratedCodeModal == "p-wix" ? `active` : ``}`} variant="outline-secondary" id="p-wix" value={'p-wix'} onClick={e => this.props.genratedCodeCallback("p-wix")}>
                                        <div className="block-center text-center h-46px w-100">
                                            <img className="img-fluid" src={baseUrl(`media/images/platform/wix${themeSkin == 'dark' ? '-dark' : ''}.svg`)} alt="wix" height={23} width={60} />
                                        </div>
                                </ToggleButton>
                                <ToggleButton className={`mb-2 bg-transparent ${state.genratedCodeModal == "p-squarespace" ? `active` : ``}`} variant="outline-secondary" id="p-squarespace" value={'p-squarespace'} onClick={e => this.props.genratedCodeCallback("p-squarespace")}>
                                    <div className="block-center text-center h-46px w-100">
                                        <img className="img-fluid" src={baseUrl(`media/images/platform/squarespace${themeSkin == 'dark' ? '-dark' : ''}.svg`)} alt="wix" height={45} width={80} />
                                    </div>
                                </ToggleButton>
                                <ToggleButton className={`mb-2 bg-transparent ${genratedCodeModal == "p-shopify" ? `active` : ``}`} variant="outline-secondary" id="p-shopify" value={'p-shopify'} onClick={e => this.props.genratedCodeCallback("p-shopify")}>
                                    <div className="block-center text-center h-46px w-100">
                                        <img className="img-fluid" src={baseUrl(`media/images/platform/shopify${themeSkin == 'dark' ? '-dark' : ''}.svg`)} alt="wix" height={25} width={90} />
                                    </div>
                                </ToggleButton>
                                <ToggleButton className={`mb-2 bg-transparent ${genratedCodeModal == "p-other" ? `active` : ``}`} variant="outline-secondary" id="p-other" value={'p-other'} onClick={e => this.props.genratedCodeCallback("p-other")}>
                                    <div className="block-center text-center h-46px w-100">
                                        <img className="img-fluid" src={baseUrl(`media/images/platform/other${themeSkin == 'dark' ? '-dark' : ''}.svg`)} alt="other" height={31} width={70} />
                                    </div>
                                </ToggleButton>
                            </ToggleButtonGroup>
                        </Accordion.Body>
                    </Accordion.Item>
                </Accordion>

            </div>
            <div className='filter_actions_'>
                <Button variant='primary' className={`w-100  ${state.isUpdating ? `spinner` : ``}`} onClick={this.props.onRequestPublish}>Save Widget</Button>
                {!isHide ? <Button variant='link' className='w-100 text-decoration-none fw-medium' onClick={onResetFilter}>Back</Button> : null}
            </div>
        </>

    }
}
const mapStateToProps = (state) => {
    return {
        templeteList: state.templates.templeteList,
        themeSkin: state.themeSkin.themeSkin
    }
}
export default connect(mapStateToProps)(WebEmbedAside);